<template lang="pug">
    .wrapper-checklist
        .header-list
            we-editable(
                class="editable-title",
                ref="title",
                :content="checklist.title",
                @update="checklist.title = $event", 
                placeholder="Titulo da checklist"
            )

        ul.checklist(v-if="checklist.items")
            li.item(v-for="(item, index) in checklist.items")
                input(type="checkbox", :name="index")
                .icon-check
                span.text {{item.text}}
                button.button-delete(@click="removeItem(item)") Remover
                //- we-shareUser(currentPage="checklists")
        
        we-editable(
            class="editable-item",
            ref="editable",
            @keyup.enter.native="onCreatedItem",
            :content="contentChecklist.text",
            @update="contentChecklist.text = $event", 
            placeholder="Digitar novo item"
        )
        
</template>

<script>
    import Vue from 'vue'
    import WeEditable from '@/components/common/We-Editable.vue'
    import WeShareUser from '@/components/common/We-ShareUser.vue'
    import WeUserThumb from '@/components/common/We-UserThumb.vue'

    export default {
        name: 'we-checklist',
        components:{
            'we-editable': WeEditable,
            'we-shareUser': WeShareUser,
            'we-userThumb': WeUserThumb
        },
        data () {
            return {
                checklist: {
                    title: null,
                    items: []
                },
                contentChecklist: {
                    text: ''
                }
            }
        },
        created(){
        },
        mounted(){
            this.$refs.editable.$el.focus();
        },
        watch: {
            checklist: {
                handler(old, val){
                    this.$emit('returnTolls', 'checklist', this.checklist);
                },
                deep: true
            }
        },
        methods: {
            removeItem(item){
                var index = this.checklist.items.indexOf(item);
                if (index > -1) {
                    this.checklist.items.splice(index, 1);
                }
            },
            onCreatedItem(){
                let item = {
                    check: false,
                    text: this.contentChecklist.text
                }

                this.checklist.items.push(item);
                this.$refs.editable.$el.innerHTML = '';
            }
        }
    }
</script>
<style lang="stylus" src="@/assets/styles/tools-created/CheckList.styl"></style>


