<template lang="pug">
    .created-box
        we-editable.title(
            ref="title",
            :content="data.title",
            @update="data.title = $event", 
            placeholder="O que é pra fazer?"
        )
        we-editable.descrition(
            ref="description",
            :content="data.description",
            @update="data.description = $event", 
            placeholder="Descrição.."
        )

        .inserts(v-for="(child, index) in inserts")
            component(:is="child.name", :key="index", @returnTolls="dataInsert")

        .files-uploads(v-if="Object.keys(data.files).length")
            .header-uploads
                h3.title Arquivos anexados

            .list-files
                .file(v-for="file in data.files")
                    img.image(:src="file.dataURL")

                    .details
                        span.filename {{file.name}}
                        span.size {{file.size}}

        .buttons
            button.button-list(@click="selectToll('list')") Lista
            button.button-checklist(@click="selectToll('checklist')") Lista
            we-upload(@upload="getUploads")

</template>

<script>
    import WeEditable from '@/components/common/We-Editable.vue'
    import WeChecklist from '@/components/tools-created/We-CheckList.vue'
    import WeList from '@/components/tools-created/We-List.vue'
    import WeUpload from '@/components/tools-created/We-Upload.vue'
    import eventHub from '@/EventHub.js'

    export default {
        name: 'w-createdBox',
        props: ['data'],
        components: {
            'we-editable': WeEditable,
            'we-checklist': WeChecklist,
            'we-list': WeList,
            'we-upload': WeUpload
        },
        data () {
            return {
                inserts: []
            }
        },
        watch: {
            data: {
                handler(old, val){
                    this.$emit('returnCreatedBox', val);
                },
                deep: true
            }
        },
        created(){
            eventHub.$on('clearCreatedBox', () => {
                this.clear();
            });
        },
        methods: {
            clear(){
                this.$refs.title.$el.innerHTML = '';
                this.$refs.description.$el.innerHTML = '';
                this.inserts = [];
            },
            getUploads(files){
                this.data.files = files;
            },
            dataInsert(type, data){
                this.data[type] = data;
            },
            selectToll(tollType){
                let toll = {
                    name: 'we-'+tollType
                }

                this.inserts.push(toll);
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/CreatedBox.styl"></style>


