<template lang="pug">
    .user-thumb
        .photo(:style="{'background-image':'url('+data.infos.photo+')'}")
        span.name {{data.infos.name}}
        button.button-removeUser(@click="removeUser(data)")
</template>

<script>
    export default {
        name: 'w-userThumb',
        props: ['data','users'],
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        mounted(){
            // console.log(this.data);
        },
        methods:{
            removeUser(user){
                var index = this.users.indexOf(user);
                if (index > -1) {
                    this.users.splice(index, 1);
                }
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/UserThumb.styl"></style>


