<template lang="pug">
    .wrapper-list
        .header-list
            we-editable(
                class="editable-title",
                ref="title",
                :content="list.title",
                @update="list.title = $event", 
                placeholder="Titulo da lista"
            )

        ul.list(v-if="list.items")
            li.item(v-for="item in list.items")
                span.text {{item.text}}
                button.button-delete(@click="removeItem(item)") Remover
        
        we-editable(
            class="editable-item",
            ref="editable",
            @keyup.enter.native="onCreatedItem",
            :content="contentList.text",
            @update="contentList.text = $event", 
            placeholder="Digitar novo item"
        )

</template>

<script>
    import Vue from 'vue'
    import WeEditable from '@/components/common/We-Editable.vue'
    
    export default {
        name: 'we-list',
        components:{
            'we-editable': WeEditable
        },
        data () {
            return {
                list: {
                    title: null,
                    items: []
                },
                contentList: {
                    text: ''
                }
            }
        },
        created(){
            
        },
        mounted(){
            this.$refs.editable.$el.focus();
        },
        watch: {
            list: {
                handler(old, val){
                    this.$emit('returnTolls', 'list', this.list);
                },
                deep: true
            }
        },
        methods: {
            removeItem(item){
                var index = this.list.items.indexOf(item);
                if (index > -1) {
                    this.list.items.splice(index, 1);
                }
            },
            onCreatedItem(){
                let item = {
                    text: this.contentList.text
                }

                this.list.items.push(item);
                this.$refs.editable.$el.innerHTML = '';
            }
        }
    }
</script>
<style lang="stylus" src="@/assets/styles/tools-created/List.styl"></style>


