<template lang="pug">
	.content-note
		.overlay(@click="close")
		.popup-modal(ref="popupNote")
			.content-popup
				span.description Essa nota sera removida
				h3.title Tem certeza que quer excluir?

				.buttons
					button.button-confirm(@click="removeNote(currentNote)") Excluir
					button.button-cancel(@click="closePopupRemove") Cancelar Exclusão

		.popup-modal(ref="popupConvert")
			.content-popup
				span.description Essa nota foi convertida
				h3.title Deseja manter essa nota

				.buttons
					button.button-confirm(@click="removeNote(currentNote)") Descartar Nota
					button.button-cancel(@click="closePopupRemoveConvert") Manter nota

		.modal-note(ref="modalNote")
			.group
				.header-content
					.users
						we-userThumb(:data="currentNote.users[0]")

						.content-user
							.text
								h3.name {{currentNote.users[0].id === masterUser.id ? 'Você' : currentNote.users[0].infos.name}}
								span.date {{this.currentNote.date}}
						
						we-userThumb(v-for="(user, index) in currentNote.users", v-if="index > 0", :data="user", :users="currentNote.users")

						we-buttonShareUser(@openShare="openShare")
						we-shareUser(:data="currentNote", v-if="isShareUserNote", @closeShare="closeShare")
						
					.wrapper-options(ref="options")
						button.button-options(@click="toggleOptions") Opções
						button.button-back(@click="toggleOptions")
						
						.buttons
							button.button-hashtag Hashtags
							we-upload(class="button-arquivos", @upload="getUploads")
							button.button-lembrete Lembretes
							button.button-list(@click="addList") Adicionar Lista
							button.button-checklist(@click="addCheckList") Adicionar Checklist

							.more
								button.button-more Mais Opções

								.buttons-more
									button.button-delete(@click="openPopupRemove") Excluir
									
				.content-text
					.content-title
						we-editable(
							class="title",
							:content="currentNote.content.title",
							@update="currentNote.content.title = $event"
						)

						.buttons
							button.button-add Salvar
							button.button-cancel Cancelar
						
					.content-description
						we-editable(
							class="description",
							:content="currentNote.content.description",
							@update="currentNote.content.description = $event"
						)

						.buttons
							button.button-add Salvar
							button.button-cancel Cancelar

					we-inserts(:data="currentNote", :addComponentPre="addInserts", @checkDeleteList="resetAddInserts", @checkDeleteCheckList="resetAddInserts")

			.footer(v-if="!converted")
				button.button-convert-task(@click="openConvert") Converter em tarefa
			
		.modal-convert(v-show="isConvert")
			.group
				.header-content
					.users
						we-userThumb(:data="currentNoteConvert.users[0]")

						.content-user
							.text
								h3.name {{currentNoteConvert.users[0].id === masterUser.id ? 'Você' : currentNoteConvert.users[0].infos.name}}
								span.date {{this.currentNoteConvert.date}}
						
						we-userThumb(v-for="(user, index) in currentNoteConvert.users", v-if="index > 0", :data="user")

					.change-board
						.place(:data-value="selecChangeBoard")
						select.select(v-model="selecChangeBoard", @change="changeBoardSelect")
							option(v-for="board in boards", :data-id="board.id", :value="board.title") {{board.title}}
					
				.content-text
					.content-title
						we-editable(
							class="title",
							:content="currentNoteConvert.content.title",
							@update="currentNoteConvert.content.title = $event"
						)

						.buttons
							button.button-add Salvar
							button.button-cancel Cancelar
						
					.content-description
						we-editable(
							class="description",
							:content="currentNoteConvert.content.description",
							@update="currentNoteConvert.content.description = $event"
						)

						.buttons
							button.button-add Salvar
							button.button-cancel Cancelar

					we-inserts(:data="currentNoteConvert")

			.footer
				button.button-convert-task(@click="saveConvert") Salvar em {{currentBoard.title}}
				button.button-cancel(@click="closeConvert") Cancelar
		
</template>

<script>
	import Notes from '@/components/notes/notes.js'
	import Boards from '@/components/boards/boards.js'
	import MasterUser from '@/master-user.js'
	import WeEditable from '@/components/common/We-Editable.vue'
	import WeShareUser from '@/components/common/We-ShareUser.vue'
	import WeInserts from '@/components/common/We-Inserts.vue'
	import WeUserThumb from '@/components/common/We-UserThumb.vue'
	import WeButtonShareUser from '@/components/common/We-ButtonShareUser.vue'
	import WeUpload from '@/components/tools-created/We-Upload.vue'

	export default {
		name: 'we-contentNote',
		components:{
			'we-inserts': WeInserts,
			'we-editable': WeEditable,
			'we-buttonShareUser': WeButtonShareUser,
			'we-shareUser': WeShareUser,
			'we-upload': WeUpload,
			'we-userThumb': WeUserThumb
		},
		data () {
			return {
				isShareUserNote: false,
				addInserts: null,
				currentBoard: {},
				showTempUsers: true,
				isConvert: false,
				converted: false,
				usersShare: [],
				currentDate: null,
				masterUser: MasterUser,
				notes: Notes.notes,
				boards: Boards.boards,
				selecChangeBoard: 'Outro Quadro',
				currentNote: null,
				currentNoteConvert: null,
				msg: 'Welcome to Your Vue.js App'
			}
		},
		created(){
			this.getNote();
		},
		watch:{
		},
		mounted(){
			this.getBoard(this.$route.params.id);
		},
		methods: {
			openShare(){
				this.isShareUserNote = true;
			},
			closeShare(){
				this.isShareUserNote = false;
			},
			resetAddInserts(){
				this.addInserts = null;
			},
			addList(){
				this.addInserts = 'list';
			},
			getUploads(files){
				if (!this.currentNote.content.files.length) {
			    	this.currentNote.content.files = files;
				}else{
					for (var i = 0; i < files.length; i++) {
			    		this.currentNote.content.files.push(files[i]);
					}
				}
			},
			addCheckList(){
				this.addInserts = 'checklist';
			},
			removeNote(note){
                var index = this.notes.indexOf(note);
                if (index > -1) {
                    this.notes.splice(index, 1);
                }

                this.converted = false;
                this.close();
            },
			openPopupRemove(){
				this.$refs.popupNote.classList.add('show');
			},
			openPopupConvert(){
				this.$refs.popupConvert.classList.add('show');
			},
			closePopupRemoveConvert(){
				this.$refs.popupConvert.classList.remove('show');
				this.converted = false;
				this.close();
			},
			closePopupRemove(){
				this.$refs.popupNote.classList.remove('show');
			},
			close(){
				if (this.converted) {
					this.openPopupConvert();
				}else{
					this.$router.go(-1);
				}
			},
			arrayMax(arr) {
				return arr.reduce(function (p, v) {
					return ( p > v ? p : v );
				});
			},
			getNextIdTask(){
				let allTasks = [];
				let mergeArrays = null;
				let tempArrayTasksIds = [];

				for (var i = 0; i < this.currentBoard.cols.length; i++) {
					allTasks.push(this.currentBoard.cols[i].tasks);
				}

				mergeArrays = [].concat.apply([], allTasks);

				for (var i = 0; i < mergeArrays.length; i++) {
					tempArrayTasksIds.push(Number(mergeArrays[i].id))
				}

				return this.arrayMax(tempArrayTasksIds) + 1;
			},
			setDateDafult(){
				let today = new Date();
				let dd = today.getDate();
				let mm = today.getMonth()+1; //January is 0!
				let yyyy = today.getFullYear();

				this.currentDate = dd+'/'+mm+'/'+yyyy;
			},
			saveConvert(){
				let tempTask = {
					id: this.getNextIdTask(),
					users: [],
					date: this.currentDate,
					content: {
						title: this.currentNoteConvert.content.title,
						description: this.currentNoteConvert.content.description,
						list: this.currentNoteConvert.content.list,
						checklist: this.currentNoteConvert.content.checklist,
						files: this.currentNoteConvert.content.files
					},
					comments:[]
				};

				for (var i = 0; i < this.currentNoteConvert.users.length; i++) {
					tempTask.users.push(this.currentNoteConvert.users[i])
				}

				this.converted = true;
				
				this.currentBoard.cols[0].tasks.unshift(tempTask);

				this.closeConvert();
			},
			openConvert(){
				this.$el.classList.add('show-convert');
				this.isConvert = true;
			},
			closeConvert(){
				this.$el.classList.remove('show-convert');
				this.isConvert = false;
			},
			changeBoardSelect(e){
				let tempBoardId = e.target.options[e.target.options.selectedIndex].getAttribute('data-id');
				this.getBoard(tempBoardId)
			},
			getBoard(id){
				for (var i = 0; i < this.boards.length; i++) {
					if (this.boards[i].id == id) {
						this.currentBoard = this.boards[i];
					}
				}
			},
			setStatusConvert(){
				this.$refs.modalNote.setAttribute('data-convert', true);
				this.currentNote.convert = true;
			},
			getNote(){
				let idNote = this.$route.params.idNote;

				for (var i = 0; i < Notes.notes.length; i++) {
					if (Notes.notes[i].id == idNote) {
						this.currentNote = Notes.notes[i];
						this.currentNoteConvert = Notes.notes[i];
					}
				}
			},
			deadline(id, el){
				let buttons = document.querySelectorAll('.deadlines button')
				
				for (var i = 0; i < buttons.length; i++) {
					buttons[i].classList.remove('active')
				}

				el.classList.add('active');
			},
			toggleOptions(){
				this.$refs.options.classList.toggle('show-options');
			}
		}
	}
</script>
<style lang="stylus" src="@/assets/styles/common/modais/ContentNote.styl"></style>


