<template lang="pug">
    .popup-confirm(ref="popup")
        .content-popup
            span.description {{description}}
            h3.title Tem certeza que quer excluir?

            .buttons
                button.button-confirm(@click="confirm") Excluir
                button.button-cancel(@click="cancel") Cancelar Exclusão
</template>

<script>
    export default {
        name: 'w-popupConfirm',
        props:{
            description: ''
        },
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        mounted:function(){
            // this.$emit('update', event.target.innerText);
        },
        methods:{
            confirm(){
                this.$emit('confirm');
            },
            cancel(){
                this.$emit('close');
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/PopupConfirm.styl"></style>


