<template lang="pug">
	.task-card(@click="openTask(data.id, idCol, $event.target)")
		.content-card(:class="data.users.length > 1 ? 'isOthers' : ''")
			.users
				we-userThumb(:data="data.users[0]", class="master")
				we-userThumb(v-for="(user, index) in data.users", v-if="index > 0", :data="user")

			.content-text
				h3.title {{data.content.title}}

				.footer-content
					.date {{data.date}}
					.count-list {{data.content.checklist ? data.content.checklist.length : '0'}}
					.count-comments {{data.comments ? data.comments.length : '0'}}
					.count-anexos {{data.content.files ? data.content.files.length : '0'}}
</template>

<script>
	import WeUserThumb from '@/components/common/We-UserThumb.vue'

	export default {
		name: 'w-colboard',
		components:{
			'we-userThumb': WeUserThumb,
		},
		props: {
			data: Object,
			idCol: Number,
			idBoard: Number
		},
		data () {
			return {
				isOthersUsers: false
			}
		},
		mounted(){
			console.log(this.data.users[0]);

			if (this.data.users.length > 1) {
				this.isOthersUsers = true
			}else{
				this.isOthersUsers = false
			}
		},
		created(){
			
		},
		methods:{
			stop(){},
			openTask(taskId, colId, item){
				let checkDrop = item.getAttribute('data-drag');
				this.$router.push({
				    name: 'task',
				    params: {
				        type: 'task',
				        idBoard: this.idBoard,
				        idTask: taskId,
				        idCol: colId
				    }
				})
			}
		}
	}
</script>
<style lang="stylus" scope src="@/assets/styles/boards/Task.styl"></style>


