<template lang="pug">
	.wrapper-created-task
		.overlay(@click="openConfirmClose")
		button.button-trash(@click="openConfirmClose")
		.group
			w-popupConfirm(
			    @confirm="close",
			    @close="cancelClose",
			    v-if="isConfirmClose", 
			    description="Se você sair, essa task sera perdida"
			)

			.users-task
				we-userThumb.master(:data="masteruser")
				we-userThumb(v-for="user in usersShare", :data="user", v-if="!user.master", :users="usersShare")
				we-buttonShareUser(@openShare="openShare")
					
			.created
				we-shareUser(@selectUsers="getUsersShare", v-if="isShareUserCreatedTask", @closeShare="closeShare")
				we-createdBox(:data="contentTask")

				.footer-created
					.deadlines
						button.button.button-today(@click="deadline(1, $event.target)").active Hoje
						button.button.button-tomorrow(@click="deadline(2, $event.target)") Amanhã
						button.wrapper-datepicker(ref="wrapperDatepicker")
							.overlay
							.placeholder Abrir calendario

							flat-pickr(class="button-date", @on-open="openPickerDate", @on-close="closePickerDate", v-model="currentDate", :config="configPicker", placeholder="Abrir calendario")

					button.button-save(:class="activeButtons ? 'active' : ''", @click="saveTask") Salvar em minhas tarefas
					button.button-cancel(:class="activeButtons ? 'active' : ''") Cancelar

</template>

<script>
	import MasterUser from '@/master-user.js'
	import WeShareUser from '@/components/common/We-ShareUser.vue'
	import WeUserThumb from '@/components/common/We-UserThumb.vue'
	import flatPickr from 'vue-flatpickr-component';
	import { Portuguese } from "flatpickr/dist/l10n/pt"
	import WePopupConfirm from '@/components/common/We-PopupConfirm.vue'

	import 'flatpickr/dist/flatpickr.css';

	import Boards from '@/components/boards/boards.js'
	import WeButtonShareUser from '@/components/common/We-ButtonShareUser.vue'
	import WeCreatedBox from '@/components/common/We-CreatedBox.vue'
	import eventHub from '@/EventHub.js'

	export default {
		name: 'w-created-task',
		components:{
			'w-popupConfirm': WePopupConfirm,
			'we-buttonShareUser': WeButtonShareUser,
			'we-createdBox': WeCreatedBox,
			'flat-pickr': flatPickr,
			'we-shareUser': WeShareUser,
			'we-userThumb': WeUserThumb
		},
		data () {
			return {
				isConfirmClose: false,
				isShareUserCreatedTask: false,
				currentBoard: {},
				configPicker: {
					dateFormat: "d/m/Y",
					altFormat: "d M Y",
					altInput: true,
					locale: Portuguese
				},
				boards: Boards.boards,
				usersShare: [],
				myDropzone: null,
				myPicker: null,
				activeButtons: true,
				currentDate: null,
				contentTask: {
					title: null,
					description: null,
					list: [],
					checklist: [],
					files: []
				},
				masteruser: MasterUser
			}
		},
		mounted(){
			this.datePicker();
			this.getBoard(this.$route.params.id);
		},
		created(){
			this.setDateDafult();
		},
		methods:{
			openShare(){
				this.isShareUserCreatedTask = true;
			},
			closeShare(){
				this.isShareUserCreatedTask = false;
			},
			datePicker(){
				this.myPicker = new flatpickr('#wrapper-datepicker', {});
			},
			openPickerDate(){
				let buttons = document.querySelectorAll('.deadlines .button')
				for (var i = 0; i < buttons.length; i++) {
					buttons[i].classList.remove('active');
				}

				let today = new Date();
				this.contentTask.date = today;

				this.$refs.wrapperDatepicker.classList.add('active','show');
			},
			closePickerDate(){
				this.$refs.wrapperDatepicker.classList.remove('show');
			},
			getUsersShare(data){
				this.usersShare = data;
			},
			getBoard(id){
				for (var i = 0; i < this.boards.length; i++) {
					if (this.boards[i].id == id) {
						this.currentBoard = this.boards[i];
					}
				}
			},
			arrayMax(arr) {
				return arr.reduce(function (p, v) {
					return ( p > v ? p : v );
				});
			},
			getNextIdTask(){
				let allTasks = [];
				let mergeArrays = null;
				let tempArrayTasksIds = [];

				for (var i = 0; i < this.currentBoard.cols.length; i++) {
					allTasks.push(this.currentBoard.cols[i].tasks);
				}

				mergeArrays = [].concat.apply([], allTasks);

				for (var i = 0; i < mergeArrays.length; i++) {
					tempArrayTasksIds.push(Number(mergeArrays[i].id))
				}

				return this.arrayMax(tempArrayTasksIds) + 1;
			},
			saveTask(){
				let tempTask = {
					id: this.getNextIdTask(),
					users: [],
					date: this.currentDate,
					content: {
						title: this.contentTask.title,
						description: this.contentTask.description,
						list: this.contentTask.list,
						checklist: this.contentTask.checklist,
						files: this.contentTask.files
					},
					comments:[]
				};

				tempTask.users.push(this.masteruser);

				for (var i = 0; i < this.usersShare.length; i++) {
					tempTask.users.push(this.usersShare[i])
				}
				
				this.currentBoard.cols[0].tasks.unshift(tempTask);

				this.clearCreated();
			},
			openConfirmClose(){
				if (this.contentTask.title || this.contentTask.description) {
					this.isConfirmClose = true;
				}else{
					this.close();
				}
			},
			cancelClose(){
				this.isConfirmClose = false;
			},
			close(){
				this.$router.go(-1);
			},
			clearCreated(){
				this.contentTask = {
					title: null,
					description: null,
					list: [],
					checklist: [],
					files: []
				}

				eventHub.$emit('clearCreatedBox');
			},
			setDateDafult(){
				let today = new Date();
				let dd = today.getDate();
				let mm = today.getMonth()+1; //January is 0!
				let yyyy = today.getFullYear();

				this.currentDate = dd+'/'+mm+'/'+yyyy;
			},
			deadline(type, el){
				let today = new Date();
				let dd = today.getDate();
				let mm = today.getMonth()+1; //January is 0!
				let yyyy = today.getFullYear();

				let buttons = document.querySelectorAll('.deadlines .button')
				this.$refs.wrapperDatepicker.classList.remove('active');

				for (var i = 0; i < buttons.length; i++) {
					buttons[i].classList.remove('active')
				}

				if (type === 1) {
					this.currentDate = dd+'-'+mm+'-'+yyyy;
				}else{
					today.setDate(today.getDate()+1);
					dd = today.getDate();
					mm = today.getMonth()+1; //January is 0!
					yyyy = today.getFullYear();
					this.currentDate = dd+'-'+mm+'-'+yyyy;
				}

				el.classList.add('active');
			}
		}
	}
</script>
<style lang="stylus" scope src="@/assets/styles/created/CreatedTask.styl"></style>


