<template lang="pug">
	.wrapper-created-note(ref="createdNote")
		span.place(@click="showCreated") Anote aqui

		.group
			.created
				we-shareUser(@selectUsers="getUsersShare", v-if="isShareUserCreatedNote", @closeShare="closeShare")
				we-createdBox(:data="contentNote")

		.footer-created
			.hashtag
				.header-hashtag
					button.button-public
					input(type="text", placeholder="#hashtag")
						
			.group-buttons
				button.button-save(@click="saveNote") Publicar
				button.button-cancel(@click="closeCreated") Cancelar

			.users-notes
				we-userThumb.master(:data="masteruser")
				we-userThumb(v-for="user in usersShare", :data="user", v-if="!user.master", :users="usersShare")
				we-buttonShareUser(@openShare="openShare")
</template>

<script>
	import MasterUser from '@/master-user.js'
	import WeShareUser from '@/components/common/We-ShareUser.vue'
	import WeUserThumb from '@/components/common/We-UserThumb.vue'

	import Notes from '@/components/notes/notes.js'

	import Boards from '@/components/boards/boards.js'
	import WeButtonShareUser from '@/components/common/We-ButtonShareUser.vue'
	import WeCreatedBox from '@/components/common/We-CreatedBox.vue'
	import eventHub from '@/EventHub.js'

	export default {
		name: 'w-created-task',
		components:{
			'we-buttonShareUser': WeButtonShareUser,
			'we-createdBox': WeCreatedBox,
			'we-shareUser': WeShareUser,
			'we-userThumb': WeUserThumb
		},
		data () {
			return {
				isShareUserCreatedNote: false,
				currentBoard: {},
				notes: Notes.notes,
				boards: Boards.boards,
				usersShare: [],
				contentNote: {
					title: null,
					description: null,
					list: [],
					checklist: [],
					files: []
				},
				masteruser: MasterUser
			}
		},
		mounted(){
			this.getBoard(this.$route.params.id);
		},
		created(){
		},
		methods:{
			openShare(){
				this.isShareUserCreatedNote = true;
			},
			closeShare(){
				this.isShareUserCreatedNote = false;
			},
			showCreated(){
			    this.$el.classList.add('show-created');
			    this.$emit('openCreated');
			},
			closeCreated(){
				this.$el.classList.remove('show-created');
				eventHub.$emit('clearCreatedBox');
			},
			getUsersShare(data){
				this.usersShare = data;
			},
			getBoard(id){
				for (var i = 0; i < this.boards.length; i++) {
					if (this.boards[i].id == id) {
						this.currentBoard = this.boards[i];
					}
				}
			},
			arrayMax(arr) {
				return arr.reduce(function (p, v) {
					return ( p > v ? p : v );
				});
			},
			getNextIdTask(){
				let allNotes = [];
				let mergeArrays = null;
				let tempArrayNotesIds = [];

				for (var i = 0; i < this.notes.length; i++) {
					allNotes.push(this.notes[i]);
				}

				mergeArrays = [].concat.apply([], allNotes);

				for (var i = 0; i < mergeArrays.length; i++) {
					tempArrayNotesIds.push(Number(mergeArrays[i].id))
				}

				return this.arrayMax(tempArrayNotesIds) + 1;
			},
			saveNote(){
				console.log(this.contentNote.list);
				
				let tempNote = {
					id: this.getNextIdTask(),
					users: [],
					date: '8 de março',
					content: {
						title: this.contentNote.title,
						description: this.contentNote.description,
						list: this.contentNote.list,
						checklist: this.contentNote.checklist,
						files: this.contentNote.files
					}
				};

				tempNote.users.push(this.masteruser);

				for (var i = 0; i < this.usersShare.length; i++) {
					tempNote.users.push(this.usersShare[i])
				}

				this.notes.unshift(tempNote);
				this.$emit('successCreatedNote');

				this.clearCreated();
			},
			clearCreated(){
				this.contentNote = {
					title: null,
					description: null,
					list: [],
					checklist: [],
					files: []
				}

				eventHub.$emit('clearCreatedBox');
			}
		}
	}
</script>
<style lang="stylus" scope src="@/assets/styles/created/CreatedNote.styl"></style>


