<template lang="pug">
	draggable(
		class="content-board",
		v-model="currentCols",
		@start="drag=true",
		:options="{group:'col', handle: '.header-col', forceFallback: true, animation: 150, scrollSpeed: 0, scrollSensitivity:0}", 
		@end="drag=false"
	)
		.col-board(v-for="(col, index) in currentCols", :key="index")
			.header-col
				h3.title {{col.title}}
				button.button-more
			
			draggable(
				class="cards",
				:ref="'cards'+col.id",
				v-model="col.tasks",
				@start="start",
				:options="{group:'cards', forceFallback: true, animation: 150, scrollSpeed: 0, scrollSensitivity:0}",
				@end="end"
			)
				transition-group(name="list", class="inner")
					we-task(v-for="(task, index) in col.tasks", :key="index", :ref="task.id", :idCol="col.id", :idBoard="currentIdBoard", :data="task")

			.footer
				.created-task(
					@click="openCreatedTaskDirectCol(col)", 
					:ref="'colCreatedDirectTask'+col.id")
					.overlay(@click.stop="closeCreatedTaskDirectCol(col)")

					we-editable(
						:ref="'editableDirect'+col.id",
						:content="contentTask.title",
						@update="contentTask.title = $event", 
						placeholder="Adicionar tarefa"
					)
					.buttons
						button.button-add(@click="saveTaskDirectCol(col)") Adicionar
						button.button-cancel(@click.stop="closeCreatedTaskDirectCol(col)") Cancelar

		.col-board-created(@click="openCreatedCol", ref="colBoardCreated")
			.backgroundOverlay(@click.stop="closeCreatedCol")
			.content-created
				we-editable(
					ref="editable",
					:content="contentColCreated.title",
					@update="contentColCreated.title = $event", 
					placeholder="Adicionar coluna"
				)

				.buttons
					button.button-add(@click.stop="createdCol") Adicionar
					button.button-cancel(@click.stop="cancelCreatedCol") Cancelar
</template>

<script>
	import WeEditable from '@/components/common/We-Editable.vue'
	import WeUserThumb from '@/components/common/We-UserThumb.vue'
	import WeTask from '@/components/boards/We-Task.vue'
	import draggable from 'vuedraggable'
	import Boards from '@/components/boards/boards.js'
	import MasterUser from '@/master-user.js'

	export default {
		name: 'w-colboard',
		components:{
			'we-editable': WeEditable,
			'we-userThumb': WeUserThumb,
			'we-task': WeTask,
			'draggable': draggable
		},
		props: {
			cols: Array,
			idBoard: Number
		},
		data () {
			return {
				masteruser: MasterUser,
				boards: Boards.boards,
				arquivados: [],
				currentDate: null,
				currentBoard: {},
				currentIdCol: null,
				currentCols: this.cols,
				currentIdBoard: this.idBoard,
				contentColCreated: {
					title: null
				},
				contentTask: {
					title: null
				},
				msg: 'Welcome to Your Vue.js App'
			}
		},
		mounted(){
			this.setDateDafult();
		},
		methods:{
			arrayMax(arr) {
				return arr.reduce(function (p, v) {
					return ( p > v ? p : v );
				});
			},
			setDateDafult(){
				let today = new Date();
				let dd = today.getDate();
				let mm = today.getMonth()+1; //January is 0!
				let yyyy = today.getFullYear();

				this.currentDate = dd+'/'+mm+'/'+yyyy;
			},
			saveTaskDirectCol(col){
				let tempArrayTasksIds = [];
				let nextIdNewTask = 0;

				for (var i = 0; i < col.tasks.length; i++) {
					tempArrayTasksIds.push(Number(col.tasks[i].id))
				}

				nextIdNewTask = this.arrayMax(tempArrayTasksIds) + 1;

				let tempTask = {
					id: nextIdNewTask,
					users: [],
					date: this.currentDate,
					content: {
						title: this.contentTask.title,
						description: null,
						list: [],
						checklist: [],
						files: []
					},
					comments:[]
				};

				tempTask.users.push(this.masteruser);
				
				col.tasks.push(tempTask);
				this.clearCreatedDirectCol(col);

				let colElCurrent = this.$refs['cards'+col.id][0].$el;
				
				setTimeout(() => {
					colElCurrent.scrollTop = colElCurrent.scrollHeight;
				}, 10);
			},
			clearCreatedDirectCol(col){
				this.contentTask = {
					title: null
				}

				this.$refs['editableDirect'+col.id][0].$el.innerHTML = '';
			},
			openCreatedTaskDirectCol(col){
				this.$refs['colCreatedDirectTask'+col.id][0].classList.add('open');
			},
			closeCreatedTaskDirectCol(col){
				this.$refs['colCreatedDirectTask'+col.id][0].classList.remove('open');
				this.clearCreatedDirectCol(col);
			},
			openCreatedCol(){
				this.$refs.colBoardCreated.classList.add('open');
			},
			cancelCreatedCol(){
				this.closeCreatedCol();
			},
			closeCreatedCol(){
				this.$refs.colBoardCreated.classList.remove('open');
			},
			createdCol(){
				let tempCol = {};
				if (this.contentColCreated.title){
					if (this.currentCols.length) {
						let lastColId = this.currentCols[this.currentCols.length-1].id;
						tempCol = {
							id: lastColId+1,
							title: this.contentColCreated.title,
							tasks: []
						}
					}else{
						tempCol = {
							id: 1,
							title: this.contentColCreated.title,
							tasks: []
						}
					}

					this.currentCols.push(tempCol);
					this.closeCreatedCol();

					this.contentColCreated.title = null;
					this.$refs.editable.$el.innerHTML = '';
				}
			},
			start(item){
				item.item.setAttribute('data-drag', true);
				document.querySelector('.boards').classList.add('show-arquivados');
			},
			end(item){
				setTimeout(() => { 
					item.item.removeAttribute('data-drag');
				}, 10);

				document.querySelector('.boards').classList.remove('show-arquivados');
			}
		}
	}
</script>
<style lang="stylus" scope src="@/assets/styles/boards/ColsBoard.styl"></style>


