<template lang="pug">
	.notes
		.overlay(@click="close")
		.content
			.header-notes
				h2.title Notas
				button.button-filter
			
			we-createdNote(@openCreated="openNotes", ref="createdNote", @successCreatedNote="successCreatedNote")

			.list-notes
				we-note(v-for="(note, index) in notes", :key="index", :data="note")

			button.open(@click="openNotes") Expandir
</template>

<script>
	import Vue from 'vue'
	import eventHub from '@/EventHub'
	import WeNote from '@/components/notes/We-Note.vue';
	import WeCreatedNote from '@/components/created/We-CreatedNote.vue';
	import Notes from '@/components/notes/notes.js'
	import Muuri from 'muuri'
	
	export default {
		name: 'we-notes',
		components:{
			'we-createdNote': WeCreatedNote,
			'we-note': WeNote
		},
		data () {
			return {
				notes: Notes.notes
			}
		},
		created(){

		},
		mounted(){
			this.drag();
			window.dispatchEvent(new Event('resize'));
		},
		methods: {
			successCreatedNote(){
				setTimeout(() => {
					this.grid.destroy();
					this.drag();
				}, 10);	
			},
			closeCreated(){
				this.$refs.createdNote.$el.classList.remove('show-created');
			},
			close(){
				document.querySelector('.main-wrapper').removeAttribute('data-show');
				document.querySelector('.main-wrapper').removeAttribute('data-share');
				this.updateGrid();
				this.closeCreated();
			},
			openNotes(){
				document.querySelector('.main-wrapper').setAttribute('data-show', 'notes');
				this.updateGrid();
			},
			updateGrid(){
				setTimeout(() => {
					window.dispatchEvent(new Event('resize'));
					this.grid.layout();
				}, 100);
			},
			drag(index){
				this.grid = new Muuri('.list-notes', {
					dragEnabled: true,
					layoutDuration: 200,
					dragSortInterval: 2,
					layout: {
						height: 800,
						rounding: true
					}
				});

				this.grid.on('dragMove', (item, event) => {
					item._element.setAttribute('data-drag', true);
				});

				this.grid.on('dragStart', (item, event) => {
					let notes = document.querySelectorAll('.notes .note');
					for (var i = 0; i < notes.length; i++) {
						notes[i].classList.add('box');
					}

					item._element.classList.remove('box')
				});

				this.grid.on('dragEnd', (item, event) => {
					let notes = document.querySelectorAll('.notes .note');
					for (var i = 0; i < notes.length; i++) {
						notes[i].classList.remove('box');
					}

					setTimeout(() => {
						item._element.removeAttribute('data-drag') 
					}, 10);
				});
			}
		}
	}
</script>
<style lang="stylus" src="@/assets/styles/notes/Notes.styl"></style>


