<template lang="pug">
    .wrapper-inserts
        .files-uploads(v-if="data.content.files.length")
            .header-uploads
                h3.title Arquivos anexados

            .list-files
                .file(v-for="file in data.content.files")
                    img.image(:src="file.dataURL")
                    .details
                        span.filename {{file.name}}
                        span.size {{file.size}}

                    button.button-delete(@click="deleteImage(file)") Remover

        .list(v-if="Object.keys(data.content.list).length")
            w-popupConfirm(
                @confirm="deleteList",
                @close="closeConfirmDeleteList",
                v-if="isConfirmDeleteList", 
                description="Essa lista sera removida"
            )

            .header
                we-editable.title-list(
                    placeholder="Digite o titulo da lista",
                    :content="data.content.list.title",
                    @update="data.content.list.title = $event"
                )

                button.button-deleteList(@click="showConfirmDeleteList") Remover lista

            .items
                .item(v-for="item in data.content.list.items")
                    we-editable.text(
                        :content="item.text",
                        @update="item.text = $event"
                    )
                    button.button-delete(@click="deleteListItem(item)") Remover

            we-editable(
                ref="addItemList",
                class="addItem",
                @keyup.enter.native="onCreatedItemList",
                :content="contentList.text",
                @update="contentList.text = $event", 
                placeholder="Digitar novo item"
            )

        .checklist(v-if="Object.keys(data.content.checklist).length")
            w-popupConfirm(
                @confirm="deleteCheckList",
                @close="closeConfirmDeleteCheckList",
                v-if="isConfirmDeleteCheckList", 
                description="Essa checklist sera removida"
            )

            .header
                we-editable.title-list(
                    placeholder="Digite o titulo do checklist",
                    :content="data.content.checklist.title",
                    @update="data.content.checklist.title = $event"
                )

                button.button-deleteCheckList(@click="showConfirmDeleteCheckList") Remover lista

            .items
                .item(v-for="item in data.content.checklist.items", :class="item.check ? 'check' : ''") 
                    button.button-check(@click="checkItem(item)")
                    
                    we-editable.text(
                        :content="item.text",
                        @update="item.text = $event"
                    )
                    
                    button.button-delete(@click="deleteCheckItem(item)") Remover

                we-editable(
                    ref="addItemChecklist",
                    class="addItem",
                    @keyup.enter.native="onCreatedItemCheckList",
                    :content="contentCheckList.text",
                    @update="contentCheckList.text = $event", 
                    placeholder="Digitar novo item"
                )

</template>

<script>
    import WeEditable from '@/components/common/We-Editable.vue'
    import WePopupConfirm from '@/components/common/We-PopupConfirm.vue'

    export default {
        name: 'w-nav',
        components: {
            'we-editable': WeEditable,
            'w-popupConfirm': WePopupConfirm
        },
        props: ['data','addComponentPre'],
        data () {
            return {
                isConfirmDeleteList: false,
                isConfirmDeleteCheckList: false,
                contentList: {
                    text: ''
                },
                contentCheckList: {
                    text: ''
                },
                msg: 'Welcome to Your Vue.js App'
            }
        },
        watch:{
            addComponentPre(){
                this.addComponent();
            }
        },
        mounted(){
        },
        methods: {
            showConfirmDeleteList(){
                this.isConfirmDeleteList = true;
            },
            showConfirmDeleteCheckList(){
                this.isConfirmDeleteCheckList = true;
            },
            closeConfirmDeleteCheckList(){
                this.isConfirmDeleteCheckList = false;
            },
            closeConfirmDeleteList(){
                this.isConfirmDeleteList = false;
            },
            deleteList(){
                this.data.content.list = [];
                this.$emit('checkDeleteList');
                this.isConfirmDeleteList = false;
            },
            deleteCheckList(){
                this.data.content.checklist = [];
                this.$emit('checkDeleteCheckList');
                this.isConfirmDeleteCheckList = false;
            },
            addComponent(){
                if (this.addComponentPre) {
                    if (this.addComponentPre === 'list') {
                        let item = {
                            title: null,
                            items: []
                        }

                        this.data.content.list = item;
                    }else if(this.addComponentPre === 'checklist'){
                        let item = {
                            title: null,
                            items: []
                        }

                        this.data.content.checklist = item;
                    }
                }
            },
            checkItem(item){
                if (item.check) {
                    item.check = false;
                }else{
                    item.check = true
                }
            },
            deleteListItem(item){
                var index = this.data.content.list.items.indexOf(item);
                if (index > -1) {
                    this.data.content.list.items.splice(index, 1);
                }
            },
            deleteCheckItem(item){
                var index = this.data.content.checklist.items.indexOf(item);
                if (index > -1) {
                    this.data.content.checklist.items.splice(index, 1);
                }
            },
            onCreatedItemList(){
                let item = {
                    text: this.contentList.text
                }

                this.data.content.list.items.push(item);

                this.$refs.addItemList.$el.innerHTML = '';
            },
            onCreatedItemCheckList(){
                let item = {
                    check: false,
                    text: this.contentCheckList.text
                }

                this.data.content.checklist.items.push(item);
                this.$refs.addItemChecklist.$el.innerHTML = '';

                // let item = {
                //     text: this.contentList.text
                // }

                // this.data.content.list.items.push(item);

                // this.list.items.push(item);
                // this.$refs.addItemList.$el.innerHTML = '';
            },
            deleteImage(file){
                var index = this.data.content.files.indexOf(file);
                if (index > -1) {
                    this.data.content.files.splice(index, 1);
                }
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/Inserts.styl"></style>


